import React, { Component } from 'react'
import Info from './Info';
import Aboutme from './Aboutme';
import EVSUB from './EVSUB';
import NGG from './NGG';
import HCW from './HCW';
import GC from './GC';

export class Grid extends Component {

  render() {
    return (
    <div> 
        <div className="container text-center">
            <section>
                <Info />
            </section>

            <section id='aboutme'>
                <Aboutme />
            </section>
            
            <section id='EVSUB'>
                <EVSUB />
            </section>

            <section id='NGG'>
                <NGG />
            </section>

            <section id='HCW'>
                <HCW />
            </section>

            <section id='GC'>
                <GC />
            </section>

        </div>
    </div>
    )
  }
}

export default Grid
