document.querySelectorAll('nav a').forEach(anchor => {
    anchor.addEventListener('click', function (e) {
        e.preventDefault();
        const target = document.querySelector(this.getAttribute('href'));
        target.scrollIntoView({ behavior: 'smooth' });
    });
});

// Function to handle scrolling
const handleScroll = (delta) => {
    const sections = document.querySelectorAll('section');
    const activeSection = document.elementFromPoint(window.innerWidth / 2, window.innerHeight / 2)?.closest('section');

    if (!activeSection) return;

    if (delta > 0) {
        // Scroll down
        const nextSection = activeSection.nextElementSibling;
        if (nextSection) {
            nextSection.scrollIntoView({ behavior: 'smooth' });
        }
    } else {
        // Scroll up
        const prevSection = activeSection.previousElementSibling;
        if (prevSection) {
            prevSection.scrollIntoView({ behavior: 'smooth' });
        }
    }
};

// Handle wheel scrolling
window.addEventListener('wheel', (e) => {
    e.preventDefault(); // Prevent default scrolling
    handleScroll(e.deltaY);
});

// Handle touch scrolling
let touchStartY = 0;
window.addEventListener('touchstart', (e) => {
    touchStartY = e.touches[0].clientY;
});

window.addEventListener('touchmove', (e) => {
    const touchEndY = e.touches[0].clientY;
    const delta = touchStartY - touchEndY;
    if (Math.abs(delta) > 30) { // Threshold for scrolling
        e.preventDefault(); // Prevent default scrolling
        handleScroll(delta);
    }
});