import React, { Component } from 'react'

export class GC extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-8 text-start text-light my-4 evsub1">
            <b className='text-bisque'>
              Geometry Calculator
            </b>

            <ul style={{ textAlign: "justify" }}>
              <p>Welcome to my Geometry Calculator project! This interactive tool allows users to easily calculate various geometric properties, making it perfect for students, educators, and anyone interested in geometry.</p>

              <strong>About the Project</strong>
              <p style={{ marginLeft: "20px" }}>This project was developed to provide a user-friendly interface for calculating the area, perimeter, and volume of different geometric shapes, including circles, triangles, rectangles, and more.</p>

              <strong>Technologies Used</strong>
              <ul style={{ listStyleType: "none", padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li><strong>HTML:</strong> The foundation of the website, structuring the layout and content.</li>
                <li><strong>CSS:</strong> Styling and visual design, ensuring an appealing user experience.</li>
                <li><strong>JavaScript:</strong> Interactive functionality, handling user inputs and calculations in real-time.</li>
              </ul>

              <strong>Features</strong>
              <ul style={{ padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li>Calculates area, perimeter, and volume for various shapes.</li>
                <li>Responsive design for accessibility on all devices.</li>
                <li>User-friendly interface with clear instructions.</li>
                <li>Interactive visualizations to help users understand concepts better.</li>
              </ul>
            </ul>
          </div>

          <div className="col-4 text-light my-4 evsub2">
            <div className="pdf-container">
              <iframe src="https://santosh0811.github.io/Geometry-Calc/" title="Number Guessing Game" width="500px" height="400px"></iframe>
            </div>

            <a href="https://santosh0811.github.io/Geometry-Calc/" target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}>
              <button id="btn1">
                <b>
                  More info!
                </b>

                <lord-icon id="LC1" src="https://cdn.lordicon.com/uxehjwfw.json" trigger="hover"
                  colors="primary:#16a9c7,secondary:#848484">
                </lord-icon>
              </button>
            </a>
          </div>
        </div>
      </div>
    )
  }
}

export default GC
