import React, { Component } from 'react'
import evoting from '../Components/Files/E-Voting System Using BlockChain.pdf';

export class EVSUB extends Component {
    render() {
        return (
            <div>
                <div className="row">
                    <div className="col-8 text-start text-light my-4 evsub1">
                        <b className='text-bisque'>
                            E-Voting System Using Blockchain
                        </b>

                        <ul>
                            <li>
                                <p><strong>Project Overview</strong></p>
                                <p style={{ textAlign: 'justify' }}>The E-Voting System utilizing blockchain technology is designed to revolutionize the voting process by ensuring transparency, security, and accessibility. Traditional voting systems often face challenges such as voter fraud, lack of transparency, and difficulties in verifying results. Our blockchain-based solution addresses these issues by leveraging the inherent benefits of blockchain technology.</p>
                            </li>

                            <li>
                                <p><strong>Key Features</strong></p>
                                <ul className='evsubul1'>
                                    <li><p><strong>Decentralization,</strong></p></li>
                                    <li><p><strong>Transparency,</strong></p></li>
                                    <li><p><strong>Security,</strong></p></li>
                                    <li><p><strong>Anonymity,</strong></p></li>
                                    <li><p><strong>Real-Time Results</strong></p></li>
                                </ul>
                            </li>

                            <li>
                                <p><strong>Technologies Used</strong></p>
                                <p>- <strong>Blockchain Framework:</strong> Ethereum, Hyperledger</p>
                                <p>- <strong>Smart Contracts:</strong> Ganache</p>
                                <p>- <strong>Web Development:</strong> React, Node.js</p>
                                <p>- <strong>Database:</strong> XAMPP</p>
                            </li>
                        </ul>


                    </div>

                    <div className="col-4 text-light my-4 evsub2">
                        <div className="pdf-container">
                            <iframe src={`${evoting}#page=28`} title='Evoting PDF'></iframe>
                        </div>

                        <a href={evoting} target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}>
                            <button id="btn1">
                                <b>
                                    More info!
                                </b>

                                <lord-icon id="LC1" src="https://cdn.lordicon.com/uxehjwfw.json" trigger="hover"
                                    colors="primary:#16a9c7,secondary:#848484">
                                </lord-icon>
                            </button>
                        </a>
                    </div>
                </div>
            </div>
        )
    }
}

export default EVSUB
