import React, { Component } from 'react'

export class HCW extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-8 text-start text-light my-4 evsub1">
            <b className='text-bisque'>
              Hyper Car Website
            </b>

            <ul style={{ textAlign: "justify" }}>
              <p>Welcome to my Hyper Car project! This website showcases some of the fastest and most advanced hyper cars in the world, providing enthusiasts with detailed information and stunning visuals.</p>

              <strong>About the Project</strong>
              <p style={{ marginLeft: "20px" }}>This project was developed to highlight the cutting-edge technology and design of hyper cars. It features an interactive gallery, specifications, and performance data for each car.</p>

              <strong>Technologies Used</strong>
              <ul style={{ listStyleType: "none", padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li><strong>HTML:</strong> The backbone of the website, providing structure and content.</li>
                <li><strong>CSS:</strong> Styling and layout, creating a visually appealing user experience.</li>
                <li><strong>JavaScript:</strong> Interactive elements, enhancing user engagement through dynamic content.</li>
              </ul>

              <strong>Features</strong>
              <ul style={{ padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li>Interactive car gallery with high-resolution images.</li>
                <li>Detailed specifications and performance metrics for each vehicle.</li>
                <li>User-friendly navigation for seamless browsing.</li>
                <li>Responsive design for optimal viewing on any device.</li>
              </ul>

              <strong>Future Enhancements</strong>
              <p style={{ marginLeft: "20px" }}>Plans for future updates include adding user reviews, comparison tools, and a blog section to discuss trends in hyper car technology.</p>
            </ul>
          </div>

          <div className="col-4 text-light my-4 evsub2">
            <div className="pdf-container">
              <iframe src="https://santosh0811.github.io/SuperCars-ShowRoom/" title="Number Guessing Game" width="500px" height="400px"></iframe>
            </div>

            <a href="https://santosh0811.github.io/SuperCars-ShowRoom/" target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}>
              <button id="btn1">
                <b>
                  More info!
                </b>

                <lord-icon id="LC1" src="https://cdn.lordicon.com/uxehjwfw.json" trigger="hover"
                  colors="primary:#16a9c7,secondary:#848484">
                </lord-icon>
              </button>
            </a>
          </div>
        </div>
      </div>
    )
  }
}

export default HCW
