import React, { Component } from 'react'
import circle from '../Components/Images/Account Circle.png';

export class Aboutme extends Component {
  render() {
    return (
      <div>
        <div className="row">
                <div className="col-8 my-4 text-start text-light aboutme1">
                    <h2>
                        ABOUT ME!
                    </h2>
                    <p>
                    <li style={{listStyleType:"none", textAlign: 'justify'}}>
                    Hello! I'm Santosh Vishwakarma, a passionate software engineer with a love for crafting innovative solutions that drive efficiency and enhance user experiences. With a strong foundation in Python, JavaScript, React, I thrive on transforming complex problems into elegant, functional code.
                    </li>
                    </p>

                    <p>
                    <li style={{listStyleType:"none", textAlign: 'justify'}}>
                    Hold a degree Bsc in Computer Science from Nagindas Khandwala College (Autonomus) Mumbai University and have 5years of experience in software development. My journey has taken me through various roles, where I've had the opportunity to work on E-Voting System Using Blockchain Project and Some great Web Applications. I believe in the importance of clean, maintainable code and follow best practices in software design and development.
                    </li>
                    </p>

                    <p>
                    <li style={{listStyleType:"none", textAlign: 'justify'}}>
                    Collaboration is at the heart of what I do. I enjoy working with cross-functional teams to bring ideas to life, ensuring that user needs are always at the forefront. I'm constantly exploring new technologies and methodologies, particularly in areas like AI, machine learning, or Data Science, to stay ahead in this rapidly evolving field.
                    </li>
                    </p>

                    <p>
                    <li style={{listStyleType:"none", textAlign: 'justify'}}>
                    When I'm not coding, you can find me Cooking, Reading tech blogs, contributing to open-source projects and Reading Geo-Political News, always looking for ways to grow and learn.
                    </li>
                    </p>

                    <p>
                    <li style={{listStyleType:"none", textAlign: 'justify'}}>
                    Feel free to connect with me or check out my projects!
                    </li>
                    </p>
                </div>
                <div className="col-4 text-light my-4 aboutme2">
                    <img src={circle} />
                    <p>
                        <li style={{listStyleType:"none"}}>
                            I'm Santosh Vishwakarma
                        </li>
                    </p>

                    <p>
                        <li style={{listStyleType:"none"}}>
                            Graduate from Nagindas Khandwala College (Autonomus)
                        </li>
                    </p>

                    <p>
                        <li style={{listStyleType:"none"}}>
                            In Bachelor of Science in Computer Science
                        </li>
                    </p>

                    <p>
                        <li style={{listStyleType:"none"}}>
                            With A+ Grade
                        </li>
                    </p>

                    <div style={{display:"flex", justifyContent:"center", gap:"10px"}}>
                        <a href="#" target="_blank" rel="noopener noreferrer">
                            <button className="btn btn-outline-dark text-light btn1css" type="button">Degree Certificate</button>
                        </a>

                        <a href="#" target="_blank" rel="noopener noreferrer">
                            <button className="btn btn-outline-dark text-light btn1css" type="button">Passing Certificate</button>
                        </a>
                    </div>

                </div>
            </div>
      </div>
    )
  }
}

export default Aboutme
