import React, { Component } from 'react'

export class NGG extends Component {
  render() {
    return (
      <div>
        <div className="row">
          <div className="col-8 text-start text-light my-4 evsub1">
            <b className='text-bisque'>
              Number Guessing Game
            </b>
            <ul style={{ textAlign: "justify" }}>
              <p>Welcome to my Number Guessing Game! This interactive game challenges players to guess a randomly generated number between 1 and 100.</p>

              <strong>How It Works</strong>
              <ul style={{ listStyleType: "none", padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li><strong>Objective:</strong> Guess the number chosen by the computer as quickly as possible.</li>
                <li><strong>Gameplay:</strong>
                  <ul style={{ listStyleType: "disc", marginLeft: "20px" }}>
                    <li>Enter your guess in the input field.</li>
                    <li>Receive feedback on whether your guess is too high, too low, or correct.</li>
                    <li>Keep guessing until you find the right number!</li>
                  </ul>
                </li>
              </ul>

              <strong>Technologies Used</strong>
              <ul style={{ listStyleType: "none", padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li><strong>HTML:</strong> The structure of the game, providing a user-friendly interface.</li>
                <li><strong>CSS:</strong> Stylish design elements that enhance the overall look and feel of the game.</li>
                <li><strong>JavaScript:</strong> The interactive logic, handling the game mechanics and user input.</li>
              </ul>

              <strong>Features</strong>
              <ul style={{ listStyleType: "none", padding: "0px", marginBottom: "20px", marginLeft: "20px" }}>
                <li>Real-time feedback on your guesses.</li>
                <li>Responsive design for playability on any device.</li>
                <li>Simple and clean interface, making it easy for anyone to jump in and play.</li>
              </ul>

              <p>Feel free to play the game below and challenge yourself or your friends!</p>
            </ul>
          </div>

          <div className="col-4 text-light my-4 evsub2">
            <div className="pdf-container">
              <iframe src="https://santosh0811.github.io/Guessing-Number-Game/" title="Number Guessing Game" width="500px" height="400px"></iframe>
            </div>

            <a href="https://santosh0811.github.io/Guessing-Number-Game/" target="_blank" rel="noopener noreferrer" style={{ textDecoration: "none" }}>
              <button id="btn1">
                <b>
                  More info!
                </b>

                <lord-icon id="LC1" src="https://cdn.lordicon.com/uxehjwfw.json" trigger="hover"
                  colors="primary:#16a9c7,secondary:#848484">
                </lord-icon>
              </button>
            </a>
          </div>


        </div>
      </div>
    )
  }
}

export default NGG
